.title {
  font-weight: 500;

  &--large {
    font-size: 39px;
    line-height: 48px;
  }

  &--medium {
    font-size: 31px;
    line-height: 38px;

    @include max(sm) {
      font-size: 18px;
      line-height: 22px;
    }
  }

  &--small {
    font-size: 24px;
    line-height: 29px;
  }
}
